import React, { useEffect, useState } from "react";
import { authService, dbService } from "fbase";
import AppRouter from "components/Router";

function App() {
  const STUDYMODE_KEYS = "studymode";
  const TIMERMODE_KEYS = "timermode";
  const [notice, setNotice] = useState("");
  const [init, setInit] = useState(false);
  const [userObj, setUserObj] = useState(null);
  useEffect(() => {
    authService.onAuthStateChanged((user) => {
      if (user) {
        getNotice();
        setUserObj({
          displayName: user.displayName,
          uid: user.uid,
          updateProfile: (args) => user.updateProfile(args),
        });
        const thisStudyMode = localStorage.getItem(STUDYMODE_KEYS);
        const thisTimerMode = localStorage.getItem(TIMERMODE_KEYS);
        if (thisStudyMode === null) {
          localStorage.setItem(STUDYMODE_KEYS, true);
        }
        if (thisTimerMode === null) {
          localStorage.setItem(TIMERMODE_KEYS, false);
        }
      } else {
        setUserObj(null);
      }
      setInit(true);
    });
  }, []);
  const refreshUser = () => {
    const user = authService.currentUser;
    setUserObj({
      displayName: user.displayName,
      uid: user.uid,
      updateProfile: (args) => user.updateProfile(args),
    });
  };

  const getNotice = async () => {
    const noticeString = await dbService
      .doc("notice/RhvYJs0ZJ0XhjrsolJRe")
      .get();
    setNotice(noticeString.data());
  };
  return (
    <>
      <div className="initial-component">
        {init ? (
          <AppRouter
            notice={notice}
            refreshUser={refreshUser}
            isLoggedIn={Boolean(userObj)}
            userObj={userObj}
          />
        ) : (
          "Initalizing..."
        )}
      </div>
    </>
  );
}

export default App;
