import { authService } from "fbase";
import React, { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUserEdit, faPowerOff } from "@fortawesome/free-solid-svg-icons";
import "../css/ProfileHome.css";

const Profile = ({ userObj }) => {
  const history = useHistory();

  const onLogOutClick = () => {
    const okLogOut = window.confirm("로그아웃하시겠습니까?");
    if (okLogOut) {
      authService.signOut();
      history.push("/");
    } else {
      return;
    }
  };
  const onMypage = (event) => {
    const TIMERMODE_KEYS = "timermode";
    const thisTimerMode = JSON.parse(localStorage.getItem(TIMERMODE_KEYS));
    if (thisTimerMode === true) {
      event.preventDefault();
      alert(
        "타이머가 켜져 있습니다. 시간을 추가하신 후에 마이페이지를 선택해주세요."
      );
    }
  };
  return (
    <div className="profile-container">
      <div className="greeting">안녕하세요 {userObj.displayName}님</div>

      <div className="btns-container">
        <div>
          <Link to="/my-page">
            <button onClick={onMypage} className="profile-edit-btn">
              <FontAwesomeIcon icon={faUserEdit} />
              &nbsp; 마이페이지
            </button>
          </Link>
        </div>
        <div>
          <button className="logout-btn" onClick={onLogOutClick}>
            <FontAwesomeIcon icon={faPowerOff} />
            &nbsp; 로그아웃
          </button>
        </div>
      </div>
    </div>
  );
};

export default Profile;
