import Profile_Main from "components/ProfileHome";
import Todo from "components/Todo";
import TodoListAssign from "components/TodoListAssign";
import React, { useEffect, useState } from "react";
import Timer from "components/Timer";
import { dbService } from "fbase";
import "../css/Home.css";
import Notice from "components/Notice";

// Todo.js만들어서 다 넣어버리기
const Home = ({ notice, userObj }) => {
  const STUDYMODE_KEYS = "studymode";
  const TIMERMODE_KEYS = "timermode";
  const superStudyMode = JSON.parse(localStorage.getItem(STUDYMODE_KEYS));
  const superTimerMode = JSON.parse(localStorage.getItem(TIMERMODE_KEYS));
  const [todoLists, setTodoLists] = useState([]);
  useEffect(() => {
    dbService
      .collection(`${userObj.uid}`)
      .orderBy("createdAt", "desc")
      .onSnapshot((snapshot) => {
        const todoArray = snapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }));
        setTodoLists(todoArray);
      });
    if (superTimerMode === false && superStudyMode === true) {
      setTimeout(function () {
        alert(
          "타이머가 꺼져있습니다. 알람을 받지 않으시려면 상단 알람 버튼을 OFF 해주세요."
        );
      }, 500);
    }
  }, []);
  const onSwitch = () => {
    const thisStudyMode = JSON.parse(localStorage.getItem(STUDYMODE_KEYS));
    const newStudyMode = !thisStudyMode;
    localStorage.setItem(STUDYMODE_KEYS, newStudyMode);
  };

  return (
    <>
      <div className="mode-switch-container">
        <h1 className="mode-switch-title">Study Timer Alarm</h1>
        {superStudyMode ? (
          <>
            <label className="switch-button-true">
              {" "}
              <input onClick={onSwitch} type="checkbox" />{" "}
              <span className="onoff-switch-true"></span>{" "}
            </label>
          </>
        ) : (
          <>
            <label className="switch-button-false">
              {" "}
              <input onClick={onSwitch} type="checkbox" />{" "}
              <span className="onoff-switch-false"></span>{" "}
            </label>
          </>
        )}
      </div>
      <div className="home-container">
        <div className="first-inner-container">
          <div className="first-box-container box-container">
            <Profile_Main className="home-profile" userObj={userObj} />
            <TodoListAssign className="home-todo-assign" userObj={userObj} />
          </div>
          <div className="second-box-container box-container">
            <Timer userObj={userObj} todoLists={todoLists} />
          </div>
        </div>
        <div className="second-inner-container">
          <div className="third-box-container box-container">
            <Todo userObj={userObj} todoLists={todoLists} />
          </div>
          <div className="fourth-box-container box-container">
            <Notice notice={notice} />
          </div>
        </div>
      </div>
    </>
  );
};

export default Home;
